import { WALL_ID } from './keys'
const initialState = { wallID: localStorage.getItem("wallId"), ownerID: localStorage.getItem('ownerId') }
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case WALL_ID: {
            const { wallID, ownerID } = action.payload
            // return { ...state, wallID, ownerID }
        }
        default:
            return { ...state }
    }

}
